/* footer */

.footer {
	padding-top: 100px;
	padding-bottom: 50px;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
}

.footer-nav .item-link {
	font-size: 18px;
	line-height: 21px;
}

.footer .logo {
	margin-bottom: 43px;
}

.footer-nav {
	margin-bottom: 28px;
}

.footer-text {
	font-size: 18px;
	line-height: 21px;
	color: #808080;
	text-decoration: none;
	margin-top: 0;
	margin-bottom: 30px;
}

.footer-link {
	color: #808080;
	text-decoration: none;
}

@media (max-width: 576px) {

	.footer-nav .list {
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
	}

	.footer-nav .item {
		margin-bottom: 10px;
	}

	.footer {
		text-align: center;
	}
}